import React, { Component } from "react";

import {
  Link
} from "react-router-dom";


class Main extends Component {
    render() {
        return (  
                <div className= "home">
                    <header>
                        <h1 className="logo">Darling Davis Doodles</h1>
                        <nav>
                            <ul className="nav_links">
                                <li><Link to="/Parents">Parents</Link></li>
                                <li><Link to="/Parents">Puppies</Link></li>
                                <li><Link to="/About">About</Link></li>
                                <li><Link to="/Home">Home</Link></li>
                            </ul>
                        </nav>
                    </header>
                    <div id="myCarousel" className="carousel slide" data-ride="carousel">
                        <ol className="carousel-indicators">
                            <li data-target="#myCarousel" data-slide-to="0" className="active">
                            <li data-target="#myCarousel" data-slide-to="1"></li>
                            <li data-target="#myCarousel" data-slide-to="2"></li>
                            <li data-target="#myCarousel" data-slide-to="3"></li>
                            <li data-target="#myCarousel" data-slide-to="4"></li>
                            </li>

                        </ol>

                        <div className="carousel-inner">
                            <div className="item active">
                                <Link to="/About">
                                    <img src={require("./img/DadBrodyPebs.JPG")} alt="First slide"  /> 
                                </Link>
                                <div className="carousel-caption">
                                    <h3>Grandpa, Brody, and Pebbles</h3>
                                </div>
                            </div>

                            <div className="item">
                                <Link to="/About">
                                    <img src={require("./img/DaxHezBrody.jpg")} alt="Second slide " />
                                </Link>
                                <div className="carousel-caption">
                                    <h3>Daxton, Hezzy, and Brody</h3>
                                </div>
                            </div>

                            <div className="item">
                                <Link to="/About">
                                    <img src={require("./img/ShawnAbra.jpg")} alt="Third slide" />
                                </Link>
                                <div className="carousel-caption">
                                    <h3>Shawn and Abra</h3>
                                </div>
                            </div>

                            <div className="item">
                                <Link to="/About">
                                    <img src={require("./img/DaxHezPebs.jpg")} alt="Third slide" />
                                </Link>
                                <div className="carousel-caption">
                                    <h3>Daxton, Hezzy, and Pebbles</h3>
                                </div>
                            </div>

                            <div className="item">
                                <Link to="/About">
                                    <img src={require("./img/ShawnHezFinDixie.jpg")} alt="Third slide" />
                                </Link>
            
                                <div className="carousel-caption">
                                    <h3>Shawn, Hezzy, Finn, and Dixie</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                    <script src="https://code.jquery.com/jquery-1.12.4.min.js" integrity="sha384-nvAa0+6Qg9clwYCGGPpDQLVpLNn0fRaROjHqs13t4Ggj3Ez50XnGQqc/r8MhnRDZ" crossorigin="anonymous"></script>
                    <script src="https://stackpath.bootstrapcdn.com/bootstrap/3.4.1/js/bootstrap.min.js" integrity="sha384-aJ21OjlMXNL5UyIl/XNwTMqvzeRMZH2w8c5cRVpzpU8Y5bApTppSuUkhZXN0VxHd" crossorigin="anonymous"></script>
                </div>
        );
    }
}

export default Main;